import React, { Suspense } from "react";
import { Button, CardKelas } from "../../../component";
const Hero = React.lazy(() => import("../../../component/Hero"));
const ChangeKelas = React.lazy(() => import("../../../component/ChangeKelas"));
const About = React.lazy(() => import("../../../component/About"));

const Hidroponik = () => {
  const HidroponikItem = [
    {
      name: "Selada Hidroponik",
      mentor: "Tholib",
      background: "./assets/img/changekelas/hidroponik/selada-hidroponik.png",
      to: "/selada-hidroponik",
      disable: false,
    },
    {
      name: "Brokoli Hidroponik",
      mentor: "Tholib",
      background: "./assets/img/changekelas/hidroponik/brokoli-hidroponik.png",
      to: "/brokoli-hidroponik",
      disable: true,
    },
    {
      name: "Kangkung Hidroponik",
      mentor: "Tholib",
      background: "./assets/img/changekelas/hidroponik/kangkung-hidroponik.png",
      to: "/kangkung-hidroponik",
      disable: true,
    },
    {
      name: "Sawi Hidroponik",
      mentor: "Tholib",
      background: "./assets/img/changekelas/hidroponik/sawi-hidroponik.png",
      to: "/sawi-hidroponik",
      disable: true,
    },
    {
      name: "Sawi Pakcoy Hidroponik",
      mentor: "Tholib",
      background:
        "./assets/img/changekelas/hidroponik/sawi-pakcoy-hidroponik.png",
      to: "/sawi-pakcoy-hidroponik",
      disable: true,
    },
    {
      name: "Strawbery Hidroponik",
      mentor: "Tholib",
      background:
        "./assets/img/changekelas/hidroponik/strawbery-hidroponik.png",
      to: "/strawbery-hidroponik",
      disable: true,
    },
    {
      name: "Tomat Hidroponik",
      mentor: "Tholib",
      background: "./assets/img/changekelas/hidroponik/tomat-hidroponik.png",
      to: "/tomat-hidroponik",
      disable: true,
    },
  ];
  return (
    <main>
      <Suspense fallback={<div>Loading...</div>}>
      <Hero page="hidroponik">
        <Button color="primary" bgcolor="bgsecondary" onClick={() => window.location.replace("/hidroponik#changekelas")}>
          Lihat Daftar Kelas
        </Button>
      </Hero>
      <About page="hidroponik">
        <Button color="primary" styles="outline">
          Pelajari Lebih Lanjut
        </Button>
      </About>
      <ChangeKelas id={"changekelas"}>
        {HidroponikItem.map((item) => {
          return (
            <CardKelas
              bgImg={item.background}
              disable={item.disable}
              url={item.to}
              mentor={item.mentor}
            >
              {item.name}
            </CardKelas>
          );
        })}
      </ChangeKelas>
      </Suspense>
    </main>
  );
};

export default Hidroponik;
